.Cases {
    position: relative;
    background-color: black;
    width: 100%;
    height: 2000px;
}

.Case {
    position: absolute;
    font-family: Comfortaa;
    cursor: pointer;
    &_main{
        text-align: center;
        background-color: #d0b5b5;
        color: #163d3f;
        font-size: 400%;
        width: 100%;
    }
    &_description {
        background-color: #d3d3d3;
        color: #d0b5b5;
        font-size: 45px;
    }
    &-1{
        width: 35.5%;
    }
}