@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Comfortaa&display=swap');

.App {
  width: 100%;
  position: relative;
}
.image {
  img {
    width: 100%;
    height: 100%;
  }
}

.menu {
  position: absolute;
  width: 108px;
  top: 56px;
  right: 84px;
  z-index: 9999;
}

.menu-block {
  position: absolute;
  top: 0px;
  left: 0px;
  height: 156px;
  width: 100%;
  z-index: 9000;
}

.menu-wrapper {
  width: 80%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 100%;
}


.MainPage {
  display: flex;
  flex-direction: column;
  padding: 0;
  overflow: hidden;
}
.mainimgbg {
  position: relative;
  background-color: aquamarine;
  height: 100vh;
  width: 100%;
  .name {
    padding-left: 55px;
    padding-right: 55px;
  }
  .photo {
    position: absolute;
    bottom: 0px;
    left: 38.75%;
    max-width: 428px;
    max-height: 1269px;
    width: 22.3%;
  }
}
.description {
  color: #d0b5b5;
  font-family: "Bebas Neue";
  font-size: 180px;
  font-weight: 400;
  position: absolute;
  bottom: 50px
  }

@media (min-width: 1200px) {
  .mainimgbg {
    height: 75vw;
  }
}

@media (min-width: 1600px) {
  .description {
    font-size: 237px;
  }
}
