@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comfortaa&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{width:100%;position:relative}.image img{width:100%;height:100%}.menu{position:absolute;width:108px;top:56px;right:84px;z-index:9999}.menu-block{position:absolute;top:0px;left:0px;height:156px;width:100%;z-index:9000}.menu-wrapper{width:80%;margin:0 auto;display:flex;align-items:center;justify-content:space-around;height:100%}.MainPage{display:flex;flex-direction:column;padding:0;overflow:hidden}.mainimgbg{position:relative;background-color:aquamarine;height:100vh;width:100%}.mainimgbg .name{padding-left:55px;padding-right:55px}.mainimgbg .photo{position:absolute;bottom:0px;left:38.75%;max-width:428px;max-height:1269px;width:22.3%}.description{color:#d0b5b5;font-family:"Bebas Neue";font-size:180px;font-weight:400;position:absolute;bottom:50px}@media (min-width: 1200px){.mainimgbg{height:75vw}}@media (min-width: 1600px){.description{font-size:237px}}

.Aboutme{width:100%;height:1392px}.Aboutme_content{display:flex;width:100%;height:100%;background:url(/site2/static/media/background.13c02eb4.png) 0 0/cover no-repeat}.Aboutme_text{padding:226px  0px 0px 20%;flex:0 0 505px;color:#ffffff;font-family:'Comfortaa', cursive;font-size:20px;font-weight:400}

.Cases{position:relative;background-color:black;width:100%;height:2000px}.Case{position:absolute;font-family:Comfortaa;cursor:pointer}.Case_main{text-align:center;background-color:#d0b5b5;color:#163d3f;font-size:400%;width:100%}.Case_description{background-color:#d3d3d3;color:#d0b5b5;font-size:45px}.Case-1{width:35.5%}

